.add-to-cart{display:flex;align-items:center;justify-content:center;flex-direction:row;width:35px;height:35px;background-color:#fff;border:none;border-radius:25px;box-shadow:0px 0px 3px 0px rgba(0,0,0,.3);color:#000;cursor:pointer;font-size:1rem;font-weight:bold;text-transform:uppercase;transition:all .5s}.add-to-cart:hover{width:120px;background-color:#dddbd7}.add-to-cart:hover span{width:85px}.add-to-cart img{width:20px;height:20px}.add-to-cart span{display:block;width:0px;font-size:.75rem;overflow:hidden;transition:all .5s;text-overflow:clip;white-space:nowrap}.add-to-cart span::before{content:"  "}